"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  PieChart,
  Pie,
  Cell,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

// VPN Proxies data
const vpnProxiesData = [
  { name: "DCH", value: 2, color: "#9333ea" },
  { name: "SES", value: 5, color: "#f97316" },
  { name: "VPN", value: 93, color: "#6366f1" },
];

// Invalid Geo data
const invalidGeoData = [
  { name: "US", value: 68, color: "#8884d8" },
  { name: "IN", value: 12, color: "#0088FE" },
  { name: "AR", value: 10, color: "#FFBB28" },
  { name: "Others", value: 8, color: "#c2c2c2" },
  { name: "FR", value: 5, color: "#FF8042" },
];

export function ComplianceIssues() {
  const cardClass = "bg-white border-gray-200 shadow-sm";
  
  return (
    <div className="mt-6">
      <div className="bg-gray-50 border border-gray-200 py-2 px-4 rounded-sm text-center shadow-sm">
        <h2 className="text-lg font-semibold">Compliance Issues</h2>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-6">
        <Card className={cardClass}>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm">VPN Proxies %</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="h-[200px] w-full">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <Pie
                    data={vpnProxiesData}
                    cx="50%"
                    cy="50%"
                    innerRadius={60}
                    outerRadius={80}
                    paddingAngle={5}
                    dataKey="value"
                  >
                    {vpnProxiesData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip
                    contentStyle={{
                      backgroundColor: "#fff",
                      border: "1px solid #e5e7eb",
                      borderRadius: "4px",
                    }}
                    formatter={(value, name) => [`${value}%`, name]}
                  />
                  <Legend />
                </PieChart>
              </ResponsiveContainer>
            </div>
          </CardContent>
        </Card>

        <Card className={cardClass}>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm">Invalid Geo %</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="h-[200px] w-full">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart
                  data={invalidGeoData}
                  margin={{
                    top: 5,
                    right: 30,
                    left: 20,
                    bottom: 5,
                  }}
                  layout="vertical"
                >
                  <CartesianGrid strokeDasharray="3 3" horizontal={false} stroke="#e5e7eb" />
                  <XAxis type="number" tick={{ fontSize: 10 }} stroke="#6b7280" />
                  <YAxis dataKey="name" type="category" scale="band" tick={{ fontSize: 10 }} stroke="#6b7280" />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: "#fff",
                      border: "1px solid #e5e7eb",
                      borderRadius: "4px",
                    }}
                    formatter={(value, name) => [`${value}%`, name]}
                  />
                  <Legend />
                  <Bar 
                    dataKey="value" 
                    name="Value %" 
                    fill="#8884d8"
                    isAnimationActive={false}
                  >
                    {invalidGeoData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Bar>
                </BarChart>
              </ResponsiveContainer>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
} 